html {
  --active-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.header__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:not(.first) {
  margin-left: 20px;
}

.nav__link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

